.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.my_footer_component {
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-size: 12px;
  color: #cecece;
  background-color: #fff;
  box-shadow: 0 -2px 3px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  min-width: 1200px;
}
.my_footer_component .inner {
  width: 900px;
  margin: 0 auto;
}
.my_footer_component .inner a {
  color: #26bf80;
}
.my_footer_component .inner a:hover {
  text-decoration: underline;
}
