.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.clearfix {
  *zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.menu_item_mixin {
  position: relative;
  text-align: center;
  height: 65px;
  line-height: 65px;
  font-size: 16px;
  color: #fff;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}
.menu_item_component {
  position: relative;
  text-align: center;
  height: 65px;
  line-height: 65px;
  font-size: 16px;
  color: #fff;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
}
.menu_item_component a {
  display: block;
  color: #fff;
}
.menu_item_component.active:after,
.menu_item_component:hover:after {
  position: absolute;
  content: '';
  display: block;
  left: 50%;
  transform: translate(-50%);
  bottom: 5px;
  height: 2px;
  background-color: #fff;
  width: 55%;
  transition: all 0.1s ease-in-out;
}
.menu_item_component.active.has_arrow:after,
.menu_item_component:hover.has_arrow:after {
  left: 20%;
  transform: translate(0);
  width: 60%;
}
.menu_item_component .menu_item_title {
  position: relative;
  text-align: center;
  height: 65px;
  line-height: 65px;
  font-size: 16px;
  color: #fff;
  cursor: pointer;
  transition: all 0.1s ease-in-out;
  display: block;
}
.header_menu_item_content a {
  display: block;
}
